@mixin flexCenter($direction, $horizontal, $align) {
  align-items: $align;
  display: flex;
  flex-direction: $direction;
  justify-content: $horizontal;
}
@mixin styleTag(
  $color,
  $fontFamily,
  $fontSize,
  $fontStyle,
  $fontWeight,
  $lineHeight,
  $letterSpace
) {
  color: $color;
  font-family: $fontFamily;
  font-size: $fontSize;
  font-style: $fontStyle;
  font-weight: $fontWeight;
  letter-spacing: $letterSpace;
  line-height: $lineHeight;
}
